.catalog {
  display: flex;
  flex-direction: column;
  align-items: left;
  padding: 20px 0 100px 0;
}

.catalog__header {
  margin: 0 auto 20px auto;
  font-size: 20px;
  line-height: 34px;
  letter-spacing: 0.06em;
  font-weight: 300;
}

.catalog__list {
  margin: 0;
  padding: 0;
  position: relative;
  display: block;
  align-items: center;
  width: 320px;
  overflow: hidden;
}

.catalog__list-item-link {
  margin: 0 16px;
  display: block;
  width: 288px;
  min-height: 392px;
  background-position: center;
  background-repeat: no-repeat;
  color: #ffffff;
  cursor: pointer;

  &--01 {
    background-image: url("../img/background-link-1.jpg");
  }

  &--02 {
    background-image: url("../img/background-link-2.jpg");
  }

  &--03 {
    background-image: url("../img/background-link-3.jpg");
  }

  &--04 {
    background-image: url("../img/background-link-4.png");
  }
}

.catalog__list-item-number {
  padding: 23px 0 0 20px;
  position: relative;
  display: block;
  font-size: 20px;
  line-height: 22px;

  &::before {
    position: absolute;
    content: "";
    width: 70px;
    height: 1px;
    top: 33px;
    left: 51px;
    background-color: #ffffff;
  }
}

.catalog__list-btn-more {
  display: none;
}

.catalog__list-item-header {
  margin: 289px 20px 15px 20px;
  display: block;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: 0.1em;
}

.catalog__slider {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.catalog__header-double {
  display: none;
}

@media (min-width: 768px) {
  .catalog {
    padding-bottom: 100px;
  }

  .catalog__header {
    margin: 100px auto 52px auto;
    font-size: 30px;
    line-height: 33px;
    letter-spacing: 0.06em;
    text-align: center;
  }

  .catalog__list {
    width: 688px;
  }

  .catalog__list-item-link {
    margin-left: 30px;
  }
}

@media (min-width: 1920px) {
  .catalog__header {
    position: relative;
    margin-bottom: 120px;

    &::before {
      position: absolute;
      content: "";
      top: 72px;
      left: 820px;
      width: 85px;
      height: 2px;
      background-color: #ffffff;
    }
  }

  .catalog__list {
    width: 1730px;
  }

  .catalog__list-item-link {
    margin-left: 75px;

    &:hover {
      opacity: 0.4;
    }
  }

  .catalog__list-item-number::before {
    top: 39px;
  }

  .catalog__list-item-number--active {
    padding-left: 127px;
  }

  .catalog__list-btn-more {
    margin-left: 17px;
    padding: 19px 30px 16px 30px;
    display: block;
    background-color: #ffffff;
    color: #000000;
    font-size: 12px;
    line-height: 13px;
    text-align: center;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    border: none;
  }

  .catalog__list-item-link--active .catalog__list-item-header {
    margin-top: 215px;
  }
}
