.promo {
  position: relative;
  margin: 0;
  padding: 106px 0 100px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url("../img/background-mobile-promo.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.promo__header {
  margin: 0 41px 35px 41px;
  font-family: "Playfair Display SC", "Arial", serif;
  font-size: 30px;
  line-height: 42px;
  font-weight: 100;
  text-align: center;
  letter-spacing: 0.09em;
  color: #ffffff;
}

.promo__btn {
  margin: 0 71px 70px 71px;
  padding: 19px 30px 17px 30px;
  font-size: 10px;
  line-height: 11px;
  letter-spacing: 0.15em;
  color: #ffffff;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.4);
  border: 1px solid #ffffff;
}

.promo__list {
  margin: 0 0 30px 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.promo__list-item {
  margin-right: 40px;

  &:nth-child(4) {
    margin-right: 0;
  }
}

.promo__link {
  display: block;
  height: 35px;
  width: 35px;
  background-color: #3b181e;
  background-position: center center;
  background-repeat: no-repeat;
  border-radius: 50%;

  &--inst {
    background-image: url("../img/icon-inst.svg");
  }

  &--fb {
    background-image: url("../img/icon-fb.svg");
  }

  &--tel {
    background-image: url("../img/icon-tel.svg");
  }

  &--vk {
    background-image: url("../img/icon-vk.svg");
  }
}

.promo__btn-down {
  margin: 0;
  padding: 0;
  width: 22px;
  height: 33px;
  background-color: transparent;
  background-image: url("../img/btn-down.svg");
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
}

.promo__btn-catalog {
  display: none;
}

@media (min-width: 768px) {
  .promo {
    padding: 245px 0 54px 0;
    background-image: none;
  }

  .promo__header {
    margin-bottom: 82px;
    font-size: 70px;
    line-height: 77px;
  }

  .promo__btn {
    margin-bottom: 211px;
    font-size: 15px;
    line-height: 17px;
  }

  .promo__link {
    width: 55px;
    height: 55px;
  }

  .promo__list {
    margin-bottom: 35px;
  }

  .promo__btn-down {
    width: 29px;
    height: 45px;
  }
}

@media (min-width: 1920px) {
  .promo {
    padding: 125px 0 54px 0;
    background: linear-gradient(0deg, #3b181e 7.33%, rgba(59, 24, 30, 0) 73%);
  }

  .promo__header {
    margin: 0 450px 80px 450px;
    font-size: 120px;
    line-height: 132px;
  }

  .promo__btn-catalog {
    position: relative;
    margin-left: auto;
    display: block;
    background-color: transparent;
    border: none;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.06em;
    color: #ffffff;
    transform: rotate(90deg);

    &::before {
      position: absolute;
      content: "";
      top: -3px;
      left: 185px;
      background-image: url(../img/header-catalog-arrow.svg);
      background-repeat: no-repeat;
      width: 25px;
      height: 25px;
      transform: rotate(270deg);
    }
  }

  .promo__list {
    margin-right: auto;
    margin-left: 0;
    margin-top: -35px;
    margin-bottom: 0;
    transform: rotate(90deg);
    height: 95px;
  }

  .promo__link {
    width: 25px;
    height: 25px;
    background-color: transparent;
    transform: rotate(270deg);
  }

  .promo__btn-down {
    background-image: url(../img/btn-down-desktop.svg);
  }
}
